<template>
  <div style="height:100%">
    <template v-if="$store.state.customerNo !== ''">
      <div class="info-div" >
        <div class="list-content detail-content list-noBack" style="min-height:4.5em;;border-bottom:1px solid rgba(255,255,255,0.15);">
          <!-- <div class="left-header">
            <div>
              <i class="hasIcon hasImg">
                  <img src="dist/img/analyse/basic_I.png"/>
              </i>
              <span>用户信息</span>
            </div>
          </div> -->
          <ul  class="form-list detail-list" style="width:calc(100% - 1.5em);padding-left:1.5em">
            <li v-for="(items, $indexs) in customerInfos" >
              <span class="label-span">{{items.name}} :&nbsp;&nbsp;</span>
              <span :title="items.value"  class="label-span">{{getValue(items.value)}}</span>
            </li>
          </ul>
        </div>
        <div class="list-content detail-content list-noBack" style="margin-top:0;height:calc(100% - 12em)">
          <div class="list-header">
            <i class="hasIcon hasImg">
              <img src="dist/img/analyse/machine.png"/>
            </i>
            <span>设备信息列表</span>
            <selects :option="selectOpt" @getSelect="getOption" style="float:right;margin-top:-0.5em"></selects>
          </div>
            <div class="all_foot_con2 table-header">
              <template v-if="deviceInfo[currentDevice['key1']] && deviceInfo[currentDevice['key1']].length > 0">
                <table class="all_foot_ta" style="padding-right: 20px">
                  <tr style="background: #305ea0;">
                    <th width="10%">表类型</th>
                    <th width="10%">表计户号</th>
                    <th width="10%">表号</th>
                    <th width="10%">初始金额</th>
                    <th width="10%">用电类别</th>
                  <!--   <th width="10%">告警金额1</th>
                    <th width="10%">告警金额2</th> -->
                  <!--   <th width="10%">开户状态</th> -->
                    <th width="20%">安装地址</th>
                  </tr>
                </table>
                <div class="overflow" >
                  <VuePerfectScrollbar style="position: relative;height:calc(100% - 0em);width:100%">
                    <table class="all_foot_ta table  " >
                      <template v-for="(item, $index) in deviceInfo[currentDevice['key1']]" style="font-size: 0.875em">
                        <tr>
                          <td width="10%">
                          <img :src="'dist/img/analyse/' + currentDevice['key1'] + '.png'" style="margin-right:0.5em;vertical-align: text-bottom;">
                          {{currentDevice['key2']}}表
                        </td>
                          <td width="10%">{{item.meterUserNo}}</td>
                          <td width="10%">{{item.deviceNo}}</td>
                          <td width="10%">{{item.initAmount}}</td>
                          <th width="10%">{{item.elecTypeName}}</th>
                         <!--  <td width="10%">{{item.alarmThreshold}}</td>
                          <td width="10%">{{item.alarmThreshold1}}</td> -->
                        <!--   <td width="10%" @click="getAccountInfo(item,$index)" >
                          <span  v-html="getAccount(item)"></span>
                          <template v-if="item.isAccount !== 0">
                            <icon v-if="showInfo" name="sort-up" style="vertical-align: bottom;"></icon>
                            <icon v-else name="sort-down"></icon>
                          </template>
                          <!--<template v-else>-->
                            <!--<icon></icon>-->
                          <!--</template>-->
                        <!-- </td> --> 
                          <td width="20%">{{item.installAddr}}</td>
                       </tr>
                        <tr v-if="item.isAccount !== 0 && showInfo" >
                          <td colspan="7" style="background:#0E1C33 ">
                            <div class="left-header">
                              <div style="padding-left:3em">
                                <i class="hasIcon hasImg">
                                  <img src="dist/img/analyse/user_I.png"/>
                                </i>
                                <span>开户资料</span>
                              </div>
                            </div>
                            <ul  class="form-list detail-list" >
                              <li v-for="(items, $indexs) in getAccountDetail(item.accountInfo)" style="width:calc(20% - 1em)">
                                <span class="label-span" style="min-width:8em">{{items.name}} :&nbsp;&nbsp;</span>
                                <span :title="items.value"  class="label-span">{{getValue(items.value)}}</span>
                              </li>
                            </ul>
                          </td>
                        </tr>
                      </template>
                    </table>
                  </VuePerfectScrollbar>
                </div>
              </template>
              <template v-else>
                <div class="emptyShowPic">
                  <div class="emptyContent">
                    <img src="dist/img/empty-data.png">
                    <p>该用户没有{{currentDevice['key2']}}表</p>
                  </div>
                </div>
              </template>
            </div>

        </div>
      </div>
    </template>
    <template v-else>
      <div class="emptyShowPic">
        <div class="emptyContent">
          <img src="dist/img/empty-data.png">
          <p>请先输入查询条件获取对应信息</p>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
  import 'vue-awesome/icons/sort-down'
  import 'vue-awesome/icons/sort-up'
  export default{
      data() {
          return {
            customerInfo: {},
            deviceInfo: {},
            deviceType: '07',
            currentIndex: -1,
            showInfo: false
          }
      },
      computed: {
          selectOpt: function () {
          return {
            cs: 'client',
            width: '8em',
            key: 'deviceType',
            data: this.getEnumItem('deviceType').obj
          }
        },
          customerInfos: function () {
              return [
                {name: '用户姓名', value: this.customerInfo.customerName || ''},
                {name: '联系电话', value: this.customerInfo.customerContact || ''},
                {name: '门牌编号', value: this.customerInfo.propertyName || ''},
                {name: '用户地址', value: this.customerInfo.customerAddr || ''},
                {name: '部门机构', value: this.customerInfo.orgName || ''},
                {name: '告警阀值1', value: this.customerInfo.alarmThreshold || '0'},
                {name: '告警阀值2', value: this.customerInfo.alarmThreshold1 || '0'},
                {name: '告警阀值3', value: this.customerInfo.alarmThreshold2 || '0'},
                {name: '透支金额', value: typeof this.customerInfo.overdraftAmount == 'undefined' ? '--' : this.customerInfo.overdraftAmount.toString()},
                {name: '建档时间', value: this.customerInfo.createTime || ''}
              ]
          },
          currentDevice: function () {
             let key = {};
             switch (this.deviceType) {
               case '07':
                   key['key1'] = 'elec';
                   key['key2'] = '电';
                   break;
               case '08':
                 key['key1'] = 'water';
                 key['key2'] = '水';
                 break;
               case '09':
                 key['key1'] = 'gas';
                 key['key2'] = '气';
                 break;
             }
             return key
        }
      },
      methods: {
        getAccountDetail: function (item) {
            return [
              {
                name: '计费类型',
                value: item.dictItemName ,
              },
              {
                name: '预留金额',
                value: item.initAmount
              },
              {
                name: '缴费金额',
                value: item.payment
              },
              {
                name: '充值金额',
                value: item.payMoney
              },
              {
                name: '购电次数',
                value: item.payCount 
              },
              {
                name: '告警金额1',
                value: item.alertFee1
              },
              {
                name: '告警金额2',
                value: item.alertFee2
              },
              {
                name: '账户余额',
                value: item.remainAmount
              },
              {
                name: '透支金额',
                value: item.overdraftFee
              },
              {
                name: '电流互感器变比',
                value:item.curTranfRto
              }
            ]
          },
          getAccountInfo: function (item, index) {
             if (item.isAccount !== 0) {
                 this.currentIndex = index;
                 this.showInfo = !this.showInfo;
             }
          },
          getAccount: function (item) {
              if(item.localControl==1) {
                 //本地费控
                if (item.isAccount === 0) {
                  return `<span style="color: #f4a951">未开户</span>`
              }
              return `<span style="color: #68d288">已开户</span>`
              }
              else {
                return `--`;
              }
          },

          getCommInfo: function () {
             let that = this;
             that.getRequest({
               url: 'customerInfo/queryDetails',
               method: 'post',
               param: {customerNo: that.$store.state.customerNo},
               success: function (data) {
                 let Data = data.data;
                 that.deviceInfo = {};
                 that.customerInfo = Data.customerInfo;
                 if (Data.electricMeter) {
                     that.deviceInfo['elec'] = Data.electricMeter
                 }
                 if (Data.waterMeter) {
                   that.deviceInfo['water'] = Data.waterMeter
                 }
                 if (Data.gasMeter) {
                   that.deviceInfo['gas'] = Data.gasMeter
                 }
               }
             }, true)
         }
      },
      mounted: function () {
        if( typeof this.$store.state.customerNo !== 'undefined' && this.$store.state.customerNo != '') {
        this.getCommInfo();
        }
      },
      watch: {
        '$store.state.customerNo': {
           handler(val, oldVal) {
             if(typeof val !== 'undefined' && val !== oldVal) {
              this.getCommInfo()
             }
           }
        }
      }
  }
</script>

<style lang="scss" scoped="scoped">
  .list-noBack{
    background: transparent!important;
    padding:1em 0!important;
    border-radius:0!important;
    width:100%;
    .left-header{
      width: 12em;
      height:100%;
      float:left;
      display: table;
      div{
        display: table-cell;
        padding-left: 2em;
        vertical-align: middle;
        img,span{
          float:left;
        }
        span{
          font-size: 0.94em;
        }
        img{
          width:1.4em;
          height:1.4em
        }
      }
      div:after{
        content: '';
        display: block;
        width:1px;
        height:0;
        clear: both;
      }
    }
    .left-header + .form-list{
      width: calc(100% - 12em);
      float:left
    }
    .list-header{
      border-bottom:0;
      padding-top:0.3em;
      padding-left:1.5em;
      font-size:0.95em
    }
  }
  .all_foot_con2 {
    border:0;
    margin-top:0.3em;
    border-radius: 2px;
    position: relative;
    float: left;
    margin-bottom: 1.2em;
    overflow: hidden;
    height: calc(100% - 3em);
    width: 100%;
    .all_foot_ta {
      width: 100%;
      text-align: center;
      line-height: 1.4em;
      height: 1.4em;
      margin-bottom:0;
      margin-left: 0;
    }
    .all_foot_ta td , .all_foot_ta th{
      padding: 0.85em 0;
      border-right: 0;
      font-size:0.91em;
    }
    .all_foot_ta th{
      padding: 1em 0;
      font-weight: 100;
    }
    .all_foot_ta td:last-child{
      border-right:0
    }

  }
  .overflow {
    overflow: hidden;
    width: 100%;
    height: calc(100% - 4em);
  }
  .overflow .all_foot_ta tr{
    cursor:pointer;
      border-bottom:1px solid rgba(255,255,255,0.15);
      border-left:1px solid rgba(255,255,255,0.15);
      border-right:1px solid rgba(255,255,255,0.15);
      padding: 0
  }
  .overflow .all_foot_ta tr:hover {
    background:rgba(255,255,255,0.05);
    color:white
  }
  .table-header{
    border-top-left-radius:0.4em;
    border-top-right-radius:0.4em;
  }
</style>



